import UserService from "../api/UserService";
import isLogin from "../utils/isLogin";

export default async function followAction({ params }) {
  if (!isLogin()) {
    if (window.confirm("로그인이 필요한 서비스 입니다. 로그인 하시겠습니까?")) {
      window.location.href = "/";
    } else {
      return;
    }
  }
  try {
    const res = await UserService.followUser(params.username);
    return res.status;
  } catch (err) {
    if (err === 401) {
      if (window.confirm("로그인이 만료되었습니다. 로그인 하시겠습니까?")) {
        window.location.href = "/";
      } else {
        throw new Error(401);
      }
    }
  }
}
