import { useCallback } from "react";
import css from "./NotFound.module.css";
import { Link } from "react-router-dom";
import WarnSvg from "../../components/Svg/WarnSvg";

export default function NotFound() {
  const refresh = useCallback(() => {
    window.location.reload();
  }, []);

  return (
    <>
      <div className={css.n404}>
        <WarnSvg />
        <div className={css.a404}>404</div>
        <h3>원하시는 페이지를 찾을 수 없습니다.</h3>
        <p>홈으로 이동하거나 다시 시도해 주세요.</p>
        <div className={css.a}>
          <Link to="/">홈으로 이동</Link>
          <a onClick={refresh}>다시 시도하기</a>
        </div>
      </div>
    </>
  );
}
